/**
 * --------------------------------------------
 * XqAdminPage admin-list.ts
 * --------------------------------------------
 */

import {
  domReady,
  parents,
  jsonFormData
} from './xq-util'
import {xqConfirm} from './xq-confirm'
const XQ_DATA_TABLE = '.xq-data-table'
const XQ_CHECK_ALL = '.xq-data-table .xq-check-all'
const XQ_COPY_BTN = '.xq-copy'
const XQ_EDIT_BTN = '.xq-edit'
const XQ_DETELE_BTN = '.xq-delete'
const XQ_ADD_BTN = '.xq-data-table .xq-add'
const XQ_BATCH_BTN = '.xq-data-table .xq-batch'

const XQ_DRAGGER_TABLE = '.xq-data-table.xq-dragger-table'

class XqAdminList {
  _table
  constructor() {
    const table = document.querySelector(XQ_DATA_TABLE)
    if (table) {
      this._table = table
      this.init()
    }
  }

  init(): void {
    const table = this._table
    if (table) {
      const tres = table.querySelectorAll('tr')
      for (const tr of tres) {
        this.bindEdit(tr)
        this.bindDelete(tr)
        this.bindChange(tr)
        this.bindCopy(tr)
      }

      this.bindCheckAll()
      this.bindAdd()
      this.bindBatch()
      this.bindDraggerTable()
    }
  }

  bindCheckAll(): void {
    const check_all = document.querySelector(XQ_CHECK_ALL)
    if (check_all) {
      check_all.addEventListener('click', event => {
        const target = event.currentTarget as HTMLInputElement
        const {checked} = target

        const table = this._table
        // eslint-disable-next-line  @typescript-eslint/no-non-null-assertion,@typescript-eslint/no-non-null-asserted-optional-chain
        const checks = table?.querySelectorAll('tr > td:first-child > input[type=checkbox]')!
        for (const check of checks) {
          if (check === target) {
            continue
          }

          if (checked) {
            (check as HTMLInputElement).checked = true
          } else {
            (check as HTMLInputElement).checked = false
          }
        }
      })
    }
  }

  bindEdit(element: Element): void {
    const edit_btn = element.querySelector(XQ_EDIT_BTN)
    if (edit_btn) {
      edit_btn.addEventListener('click', event => {
        const target = event.currentTarget as HTMLElement
        const table = this._table
        // eslint-disable-next-line  @typescript-eslint/no-non-null-assertion,@typescript-eslint/no-non-null-asserted-optional-chain
        const pid = table?.getAttribute('pid')!
        // eslint-disable-next-line @typescript-eslint/no-non-null-assertion
        let id = element.getAttribute('id')!
        id = id.replace('xq_', '')
        if (id) {
          // eslint-disable-next-line @typescript-eslint/no-non-null-assertion
          let url = target.getAttribute('xq-url')!
          if (!url) {
            url = window.location.href
            if (url.includes('.html')) {
              url = url.slice(0, Math.max(0, url.lastIndexOf('/')))
              url = url + '/edit.html?id=' + id
            } else {
              if (pid) {
                url = url.replace('/' + pid, '')
              }

              url = url.slice(0, Math.max(0, url.lastIndexOf('/')))
              url = url + '/edit/' + id
            }
          }

          window.location.href = url
        } else {
          xqConfirm({
            content: '找不到tr的id属性！'
          })
        }
      })
    }
  }

  bindCopy(element: Element): void {
    const copy_btn = element.querySelector(XQ_COPY_BTN)
    if (copy_btn) {
      copy_btn.addEventListener('click', () => {
        // eslint-disable-next-line @typescript-eslint/no-non-null-assertion
        let id = element.getAttribute('id')!
        id = id.replace('xq_', '')
        if (id) {
          this.copy(id)
          xqConfirm({
            content: '已经将id复制到粘贴板'
          })
        }
      })
    }
  }

  copy(text: string): void {
    // text是复制文本
    // 创建input元素
    const el = document.createElement('input')
    // 给input元素赋值需要复制的文本
    el.setAttribute('value', text)
    // 将input元素插入页面
    document.body.append(el)
    // 选中input元素的文本
    el.select()
    // 复制内容到剪贴板
    document.execCommand('copy')
    // 删除input元素
    el.remove()
  }

  bindDelete(element: Element): void {
    const delete_btn = element.querySelector(XQ_DETELE_BTN)
    if (delete_btn) {
      delete_btn.addEventListener('click', event => {
        const target = event.currentTarget as HTMLElement
        const table = this._table
        // eslint-disable-next-line  @typescript-eslint/no-non-null-assertion,@typescript-eslint/no-non-null-asserted-optional-chain
        const pid = table?.getAttribute('pid')!
        // eslint-disable-next-line @typescript-eslint/no-non-null-assertion
        let id = element.getAttribute('id')!
        id = id.replace('xq_', '')
        if (id) {
          // eslint-disable-next-line @typescript-eslint/no-non-null-assertion
          let url = target.getAttribute('xq-url')!
          if (!url) {
            url = window.location.href
            if (pid) {
              url = url.replace('/' + pid, '')
            }

            url = url.slice(0, Math.max(0, url.lastIndexOf('/')))
            url += '/delete'
            if (url.includes(':3000')) {
              url = url.replace(':3000', ':9000')
            }
          }

          xqConfirm({
            content: '确定要删除吗？',
            confirm() {
              // eslint-disable-next-line @typescript-eslint/no-floating-promises
              fetch(url, {
                body: JSON.stringify({id}),
                headers: {
                  'content-type': 'application/json'
                },
                method: 'POST'
              })
              .then(async response => {
                return response.json()
              }).then(data => {
                xqConfirm({
                  // eslint-disable-next-line @typescript-eslint/no-unsafe-assignment,@typescript-eslint/no-unsafe-member-access
                  content: data.message,
                  confirm() {
                    // eslint-disable-next-line @typescript-eslint/no-unsafe-member-access
                    if (data.code === 200) {
                      window.location.reload()
                    }
                  }
                })
              })
            }
          })
        } else {
          xqConfirm({
            content: '找不到tr的id属性！'
          })
        }
      })
    }
  }

  bindChange(element: Element) {
    const table = this._table
    let id = element.getAttribute('id')
    // eslint-disable-next-line  @typescript-eslint/no-non-null-assertion,@typescript-eslint/no-non-null-asserted-optional-chain
    id = id?.replace('xq_', '')!
    const textes = element.querySelectorAll('input,select')

    for (const text of textes) {
      text.addEventListener('change', event => {
        const targetElement = event.currentTarget as HTMLInputElement
        let value = null
        value = targetElement.type === 'checkbox' ? targetElement.checked : targetElement.value

        const target_id = targetElement.getAttribute('id')
        const field = target_id?.slice(0, Math.max(0, target_id.indexOf('_')))
        const pid = table?.getAttribute('pid')
        let xq_url = table?.getAttribute('xq-url')
        const data = {id, field, value}
        if (!xq_url) {
          let url = window.location.href
          if (pid) {
            url = url.replace('/' + pid, '')
          }

          url = url.slice(0, Math.max(0, url.lastIndexOf('/')))
          url += '/change'
          xq_url = url
        } else {
          xq_url += '/change'
        }

        if (xq_url.includes(':3000')) {
          xq_url = xq_url.replace(':3000', ':9000')
        }

        // eslint-disable-next-line @typescript-eslint/no-floating-promises
        fetch(xq_url, {
          body: JSON.stringify(data),
          headers: {
            'content-type': 'application/json'
          },
          method: 'POST'
        })
        .then(async response => {
          return response.json()
        }).then(data => {
          console.log(data)
        })
      })
    }
  }

  bindAdd(): void {
    const add_btn = document.querySelector(XQ_ADD_BTN)
    if (add_btn) {
      add_btn.addEventListener('click', event => {
        const target = event.currentTarget as HTMLElement
        const table = parents(target, 'table')[0] as HTMLElement
        // eslint-disable-next-line @typescript-eslint/no-non-null-assertion
        const pid = table.getAttribute('pid')!
        // eslint-disable-next-line @typescript-eslint/no-non-null-assertion
        let url = target.getAttribute('xq-url')!
        if (!url) {
          url = window.location.href
          if (url.includes('.html')) {
            url = url.slice(0, Math.max(0, url.lastIndexOf('/')))
            url += '/add.html'
          } else {
            if (pid) {
              url = url.replace('/' + pid, '')
            }

            url = url.slice(0, Math.max(0, url.lastIndexOf('/')))
            url += pid ? '/add/' + pid : '/add'
          }
        }

        window.location.href = url
      })
    }
  }

  bindBatch(): void {
    const batch_btns = document.querySelectorAll(XQ_BATCH_BTN)
    if (batch_btns) {
      for (const batch_btn of batch_btns) {
        batch_btn.addEventListener('click', event => {
          const target = event.currentTarget as HTMLElement
          const table = parents(target, 'table')[0] as HTMLElement
          // eslint-disable-next-line @typescript-eslint/no-non-null-assertion
          const pid = table.getAttribute('pid')!
          const formObject = parents(target, 'form')[0] as HTMLFormElement
          const action = target.getAttribute('name')
          if (action) {
            // eslint-disable-next-line @typescript-eslint/no-non-null-assertion
            let url = target.getAttribute('xq-url')!
            if (!url) {
              url = window.location.href
              if (pid) {
                url = url.replace('/' + pid, '')
              }

              url = url.slice(0, Math.max(0, url.lastIndexOf('/')))
              url = url + '/' + action
              if (url.includes(':3000')) {
                url = url.replace(':3000', ':9000')
              }
            }

            const formData = jsonFormData(formObject)
            const jsonData = JSON.stringify(formData)
            if (jsonData !== '{}') {
              xqConfirm({
                content: '确定要进行批量操作吗？',
                confirm() {
                  // eslint-disable-next-line @typescript-eslint/no-floating-promises
                  fetch(url, {
                    body: jsonData,
                    headers: {
                      'content-type': 'application/json'
                    },
                    method: 'POST'
                  })
                  .then(async response => {
                    return response.json()
                  }).then(data => {
                    xqConfirm({
                      // eslint-disable-next-line @typescript-eslint/no-unsafe-assignment,@typescript-eslint/no-unsafe-member-access
                      content: data.message,
                      confirm() {
                        // eslint-disable-next-line @typescript-eslint/no-unsafe-member-access
                        if (data.code === 200) {
                          window.location.reload()
                        }
                      }
                    })
                  })
                }
              })
            } else {
              xqConfirm({
                content: '没有选择操作数据.'
              })
            }
          }
        })
      }
    }
  }

  bindDraggerTable(): void {
    const dragger_table = document.querySelector(XQ_DRAGGER_TABLE)
    // @ts-expect-error
    if (dragger_table && typeof sortable !== undefined) {
      const tbody = dragger_table.querySelector('tbody') as HTMLElement

      if (tbody) {
        // eslint-disable-next-line  @typescript-eslint/no-non-null-assertion
        const firstTr = tbody.querySelector('tbody>tr:first-child')!
        // eslint-disable-next-line  @typescript-eslint/no-non-null-assertion
        const firstId = firstTr.getAttribute('id')!
        const firstTd = firstTr.querySelector('td:first-child')
        firstTd?.classList.add('xq-move')
        tbody.setAttribute('id', 'tbody_' + firstId)
        const trs = tbody.querySelectorAll('tbody>tr:not(:first-child)')
        for (const tr of trs) {
          const td = tr.querySelector('td:first-child')
          td?.classList.add('xq-move')
          const innerbody = document.createElement('tbody')
          innerbody.append(tr)
          // eslint-disable-next-line  @typescript-eslint/no-non-null-assertion
          const trId = tr.getAttribute('id')!
          innerbody.setAttribute('id', 'tbody_' + trId)
          tbody.after(innerbody)
        }
      }

      // @ts-expect-error
      // eslint-disable-next-line  @typescript-eslint/no-unsafe-call
      sortable(XQ_DRAGGER_TABLE, {
        items: 'tbody',
        handle: '.xq-move',
        placeholder: '<tbody><tr><td colspan="99">&nbsp;</td></tr><tbody>'
      })

      dragger_table.addEventListener('sortstop', () => {
        const rows: Array<{id: string;ordernum: number}> = []
        let rowIndex = 0
        const tbodies = dragger_table.querySelectorAll('tbody')
        for (const tbody of tbodies) {
          rowIndex++
          const tr = tbody.querySelector('tr:first-child')
          // eslint-disable-next-line @typescript-eslint/no-non-null-assertion,@typescript-eslint/no-non-null-asserted-optional-chain
          const trId = tr?.getAttribute('id')!
          const row: {id: string;ordernum: number} = {id: trId, ordernum: rowIndex}
          rows.push(row)
        }

        // eslint-disable-next-line @typescript-eslint/no-non-null-assertion
        const pid = dragger_table.getAttribute('pid')!
        // eslint-disable-next-line @typescript-eslint/no-non-null-assertion
        let url = dragger_table.getAttribute('xq-url')!
        if (!url) {
          url = window.location.href
          if (pid) {
            url = url.replace('/' + pid, '')
          }

          url = url.slice(0, Math.max(0, url.lastIndexOf('/')))
          url += '/b-order'
          if (url.includes(':3000')) {
            url = url.replace(':3000', ':9000')
          }
        }

        // eslint-disable-next-line @typescript-eslint/no-floating-promises
        fetch(url, {
          body: JSON.stringify(rows),
          headers: {
            'content-type': 'application/json'
          },
          method: 'POST'
        })
        .then(async response => {
          return response.json()
        }).then(data => {
          // eslint-disable-next-line @typescript-eslint/no-unsafe-member-access
          if (data.code === 200) {
            // window.location.reload()
          }
        })
      })
    }
  }
}
domReady(() => {
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  const admin_list = new XqAdminList()
})

export default XqAdminList
