/**
 * --------------------------------------------
 * XqAdminPage tabs-toolbar.ts
 * --------------------------------------------
 */

import {
  domReady
} from './xq-util'

const FULLSCREEN_BTN_SELECTOR = '#xqkeji-tab-fullscreen'
const LEFT_BTN_SELECTOR = '#xqkeji-tab-left'
const RIGHT_BTN_SELECTOR = '#xqkeji-tab-right'
const TAB_NAV_ID = '#xqkeji-tab-nav'
const TAB_NAV_UL_ID = '#xqkeji-tab-nav-ul'
const TAB_ACTIVE = '#xqkeji-tab-nav-ul>li>a.active'
const TAB_RIGT_MENU = '#xqkeji-tab-contentmenu'
class XqTabsToolbar {
  _tab: HTMLElement
  _tab_ul: HTMLElement
  _current_tab: HTMLElement
  _right_menu: HTMLElement
  constructor() {
    // eslint-disable-next-line @typescript-eslint/no-non-null-assertion
    this._tab = document.querySelector(TAB_NAV_ID)!
    // eslint-disable-next-line @typescript-eslint/no-non-null-assertion
    this._tab_ul = document.querySelector(TAB_NAV_UL_ID)!
    // eslint-disable-next-line @typescript-eslint/no-non-null-assertion
    this._current_tab = document.querySelector(TAB_ACTIVE)!
    // eslint-disable-next-line @typescript-eslint/no-non-null-assertion
    this._right_menu = document.querySelector(TAB_RIGT_MENU)!
  }

  fullScreen(): void {
    // @ts-expect-error
    const isFullScreen = document.fullscreenElement ?? document.msFullscreenElement ?? document.mozFullScreenElement ?? document.webkitFullscreenElement ?? false // eslint-disable-line @typescript-eslint/no-unsafe-assignment
    if (!isFullScreen) {
      const de = document.documentElement
      // @ts-expect-error
      const requestMethod = de.requestFullScreen || de.webkitRequestFullScreen || de.mozRequestFullScreen || de.msRequestFullScreen // eslint-disable-line @typescript-eslint/no-unsafe-assignment
      if (requestMethod) {
        // eslint-disable-next-line @typescript-eslint/no-unsafe-member-access , @typescript-eslint/no-unsafe-call
        requestMethod.call(de)
      }
    } else {
      // @ts-expect-error
      const exitMethod = document.exitFullscreen ||	document.webkitExitFullscreen || document.msExitFullscreen
      if (exitMethod) {
        // eslint-disable-next-line @typescript-eslint/no-floating-promises
        exitMethod.call(document)
      }
    }
  }

  activeTab(): Element | null {
    const tab_ul = this._tab_ul
    // eslint-disable-next-line @typescript-eslint/no-non-null-assertion
    const active = tab_ul.querySelector('.active')!
    return active
  }

  scrollLeft(): void {
    const tab = this._tab
    const tab_ul = this._tab_ul
    // eslint-disable-next-line @typescript-eslint/no-non-null-assertion
    const first_el: HTMLElement = tab_ul.querySelector('li:first-child')!

    const abs_left_val: number = Math.abs(Number.parseInt(first_el.style.marginLeft, 10))
    const margin_left: number = Number.isNaN(abs_left_val) ? 0 : abs_left_val
    const tab_all_width = tab_ul.clientWidth + margin_left
    const visibleWidth = tab.clientWidth
    let scrollVal = 0

    if (tab_all_width > visibleWidth) {
      const active = this.activeTab()
      const lis = tab_ul.querySelectorAll('li>a')
      let preve_width = 0

      for (const li of lis) {
        if (li === active) {
          break
        }

        preve_width += li.clientWidth
      }

      const max_scroll_width = tab_all_width - visibleWidth

      scrollVal = preve_width > max_scroll_width ? max_scroll_width : preve_width

      first_el.style.marginLeft = (-scrollVal).toString() + 'px'
    } else {
      first_el.style.marginLeft = '0px'
    }
  }

  scrollRight(): void {
    const tab = this._tab
    const tab_ul = this._tab_ul
    // eslint-disable-next-line @typescript-eslint/no-non-null-assertion
    const first_el: HTMLElement = tab_ul.querySelector('li:first-child')!
    const margin_left: number = Number.isNaN(Number.parseInt(first_el.style.marginLeft, 10)) ? 0 : Number.parseInt(first_el.style.marginLeft, 10)
    const abs_left_val: number = Math.abs(margin_left)
    const tab_all_width = tab_ul.clientWidth + abs_left_val
    const visibleWidth = tab.clientWidth
    let scrollVal = 0
    if (tab_all_width > visibleWidth) {
      const active = this.activeTab()
      const lis = tab_ul.querySelectorAll('li>a')
      let preve_width = 0

      for (const li of lis) {
        preve_width += li.clientWidth
        if (li === active) {
          break
        }
      }

      if (margin_left < 0) {
        scrollVal = visibleWidth - preve_width
        first_el.style.marginLeft = scrollVal < 0 ? scrollVal.toString() + 'px' : '0px'
      }
    } else {
      first_el.style.marginLeft = '0px'
    }
  }

  bindRightMenu(li: Element): void {
    li.addEventListener('contextmenu', event => {
      event.preventDefault()
      const target = event.currentTarget as HTMLElement
      this._current_tab = target
      // @ts-expect-error
      const pageY = event.pageY as string
      // @ts-expect-error
      const pageX = event.pageX as string
      const right_menu = this._right_menu
      right_menu.style.top = pageY + 'px'
      right_menu.style.left = pageX + 'px'
      right_menu.style.display = 'block'
    })
  }

  rightMenu(): void {
    const tab_ul = this._tab_ul
    if (tab_ul !== null) {
      const lis = tab_ul.querySelectorAll('li>a')
      for (const li of lis) {
        this.bindRightMenu(li)
      }
    }
  }

  closeRightMenu(e: Event): void {
    const target = e.currentTarget as HTMLElement
    let right_menu = this._right_menu
    if (right_menu === null) {
      // eslint-disable-next-line  @typescript-eslint/no-non-null-assertion
      right_menu = window.parent.document.querySelector(TAB_RIGT_MENU)!
    }

    if (right_menu && !right_menu.contains(target)) {
      right_menu.style.display = 'none'
    }
  }

  closeTabs(isAll: boolean): void {
    if (isAll) {
      // 关闭全部
      const tabs = this._tab_ul
      const closes = tabs.querySelectorAll('li a button.close')
      for (const close of closes) {
        // @ts-expect-error
        // eslint-disable-next-line @typescript-eslint/no-unsafe-call
        close.click()
      }

      // 激活新的标签页
      // eslint-disable-next-line  @typescript-eslint/no-non-null-assertion
      const first_li = tabs.querySelector('li:first-child')!
      const first_tab = first_li.querySelector('a')
      first_tab?.classList.add('acitve')
      // eslint-disable-next-line  @typescript-eslint/no-non-null-assertion ,@typescript-eslint/no-non-null-asserted-optional-chain
      const first_content_id = first_tab?.getAttribute('data-bs-target')!
      // eslint-disable-next-line  @typescript-eslint/no-non-null-assertion
      const first_content = document.querySelector(first_content_id)!

      if (first_content) {
        first_content?.classList.add('acitve')
      }

      // 左边距
      if (first_li) {
        // @ts-expect-error
        // eslint-disable-next-line  @typescript-eslint/no-unsafe-member-access
        first_li.style.marginLeft = '0px'
      }
    } else {
      // 除此之外全部删除
      // 关闭全部
      const tabs = this._tab_ul
      const closes = tabs.querySelectorAll('li a:not(.active)')
      for (const close of closes) {
        // eslint-disable-next-line @typescript-eslint/no-non-null-assertion
        const c = close.querySelector('button.close')!
        // @ts-expect-error
        // eslint-disable-next-line @typescript-eslint/no-unsafe-call
        c.click()
      }

      const tab = this._current_tab
      // 激活新的标签页
      tab.classList.add('acitve')
      // eslint-disable-next-line  @typescript-eslint/no-non-null-assertion ,@typescript-eslint/no-non-null-asserted-optional-chain
      const content_id = tab?.getAttribute('data-bs-target')!
      // eslint-disable-next-line  @typescript-eslint/no-non-null-assertion
      const first_content = document.querySelector(content_id)!
      // eslint-disable-next-line  @typescript-eslint/no-non-null-assertion
      const first_li = tabs.querySelector('li:first-child')!
      if (first_content) {
        first_content?.classList.add('acitve')
      }

      if (first_li) {
        // 左边距
        // @ts-expect-error
        // eslint-disable-next-line  @typescript-eslint/no-unsafe-member-access
        first_li.style.marginLeft = '0px'
      }
    }
  }

  bindRightMenuOper(): void {
    const refresh_tab_id = '#xq-tab-refresh'
    const tab_close_all_id = '#xq-tab-close-all'
    const tab_close_other_id = '#xq-tab-close-other'
    // eslint-disable-next-line  @typescript-eslint/no-non-null-assertion
    const refresh_tab = document.querySelector(refresh_tab_id)!
    // eslint-disable-next-line  @typescript-eslint/no-non-null-assertion
    const tab_close_all = document.querySelector(tab_close_all_id)!
    // eslint-disable-next-line  @typescript-eslint/no-non-null-assertion
    const tab_close_other = document.querySelector(tab_close_other_id)!
    if (refresh_tab !== null) {
      refresh_tab.addEventListener('click', event => {
        event.preventDefault()
        const tab = this._current_tab
        const target_id = tab.getAttribute('data-bs-target')
        if (target_id !== null) {
          // eslint-disable-next-line  @typescript-eslint/no-non-null-assertion
          const target_el = document.querySelector(target_id)!
          if (target_el !== null) {
            const iframe = target_el.querySelector('iframe')
            if (iframe !== null) {
              iframe.contentWindow?.location.reload()// 带参数刷新
            }
          }
        }
      })
    }

    if (tab_close_all !== null) {
      tab_close_all.addEventListener('click', event => {
        event.preventDefault()
        this.closeTabs(true)
      })
    }

    if (tab_close_other) {
      tab_close_other.addEventListener('click', event => {
        event.preventDefault()
        this.closeTabs(false)
      })
    }
  }

  /* refreshTab(tab: HTMLElement): void {
     const target_id = tab.getAttribute('data-bs-target')
  } */

  init(): void {
    // 处理全屏
    const fullscreen = document.querySelector(FULLSCREEN_BTN_SELECTOR)
    if (fullscreen !== null) {
      fullscreen.addEventListener('click', event => {
        event.preventDefault()
        this.fullScreen()
      })
    }

    const leftBtn = document.querySelector(LEFT_BTN_SELECTOR)
    if (leftBtn !== null) {
      leftBtn.addEventListener('click', event => {
        event.preventDefault()
        this.scrollLeft()
      })
    }

    // 右键菜单关闭
    const {body} = document
    body.addEventListener('click', event => {
      this.closeRightMenu(event)
    })

    this.rightMenu()
    this.bindRightMenuOper()
    const rightBtn = document.querySelector(RIGHT_BTN_SELECTOR)
    if (rightBtn !== null) {
      rightBtn.addEventListener('click', event => {
        event.preventDefault()
        this.scrollRight()
      })
    }
    // 结束处理全屏
  }
}
const tabs_toolbar = new XqTabsToolbar()
domReady(() => {
  tabs_toolbar.init()
})
export {XqTabsToolbar, tabs_toolbar}

